import React from "react";
import "./aboutsub.css";
import img7 from "../Images/Aboutsub/img7.jpg";
import img8 from "../Images/Aboutsub/img8.jpg";
import img9 from "../Images/Aboutsub/img9.jpg";
import img10 from "../Images/Aboutsub/img10.jpg";
// import img11 from "../Images/Aboutsub/img11.jpg";

const Aboutsub = () => {
  return (
    <div className="aboutsub-container">
      <div className="aboutsub2">
        <h1>ele</h1>
      </div>
      <div className="aboutsub-bottle">
        <img
          src={img7}
          alt=""
          style={{
            transform: "scale(0.6)",
            justifyContent: "space-between",
            position: "absolute",
            top: "-20rem",
            left: "-120px",
            opacity: "1"
          }}
        />
        <img
          src={img8}
          alt=""
          style={{
            transform: "scale(0.6)",
            justifyContent: "space-between",
            position: "absolute",
            top: "-11rem",
            right: "-140px",
            opacity: "1",

          }}
        />
        <img
          src={img9}
          alt=""
          style={{
            position: "absolute",
            left: "200px",
            top: "-5px",
            transform: "scale(0.8)",
          }}
        />
        <img
          src={img10}
          alt=""
          style={{
            position: "absolute",
            left: "350px",
            top: "160px",
            transform: "scale(0.9)",
          }}
        />
      </div>
 
    </div>
  );
};

export default Aboutsub;
