import React from 'react'
import "./feature.css"
import img14 from "../Images/purchase/img14.jpg";
import img15 from "../Images/purchase/img15.jpg";
import img16 from "../Images/purchase/img16.jpg";
import img17 from "../Images/purchase/img17.jpg";

const Feature = () => {
  return (
    <div className='feature-container'>
        <img src={img14} alt=""  width="100%"/>
        <img src={img15} alt=""  width="100%"/>
        <img src={img16} alt="" width="100%"/>
        <img src={img17} alt="" width="100%"/>
    </div>
  )
}

export default Feature