import React from "react";
import styled from "styled-components";
import "./navbar.css";
import IMG1 from "../Images/logo.jpg";
import { MenuOutlined } from "@material-ui/icons";
import { ArrowDownward } from "@material-ui/icons";

const Container = styled.div`
  height: 700px;
  width: 100%;
  position: relative;
`;

const Wrapper = styled.div`
  padding: 10px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 95px;
`;

const Left = styled.div`
  flex: 0.9;
  display: flex;
  align-items: center;
  color: white;
  font-weight: 200;
  align-item: center;
  cursor: pointer;
  zindex: "10";
  margin-left: 40px;
`;

// const MenuOutlined  = styled.div`

// `

const Center = styled.div`
  flex: 1.2;
  text-align: center;
  cursor: pointer;
  zindex: "10";
  transform: scale(0.5);
`;

const Right = styled.span`
  flex: 1.1;
  display: flex;
  align-items: center;
  gap: 18px;
  color: #fff;
  font-size: 13px;
  font-weight: bolder:
  zIndex: "10";
  font-weight: bold;
`;

const Beer = styled.div``;

